
.userContainer{
    list-style: none;
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;
    gap:10px;
    background-color: #f3e9e7;
    position: relative;
}
.usersOnPage{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap:5px;
    margin: 10px 0;
}
.sorryMessage{
    font-size: 16px;
    font-style: italic;
}
.successSend{
    background-color: green;
    padding: 20px;
    border-radius: 20%;
}
.successInfoMessage{
    box-shadow: 0 0 8px #00a7c7;
    position: fixed;
    background-color: white;
    top:45%;
    left:45%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 10px;
    border-radius: 10px;
}