@mixin flexContainer($flexDirection, $alignItems: stretch, $justifyContent: flex-start) {
    display: flex;
    flex-direction: $flexDirection;
    justify-content: $justifyContent;
}
.colorDiv {
    background-color: #f3e9e7;
    @include flexContainer(row);
    height: 100%;
    width: 100%;
    overflow: hidden;
    position: relative;
}
.dialogColumn {
    background-color: #00a7c7;
    /*flex-basis: 20%;*/
    @include flexContainer(column);
    text-align: center;
    overflow: scroll;
    width: 100%;
   /* @media screen and (max-width:500px)  {
        position: absolute;
        top:0;
        left: 0;
        z-index: 100;
        width: 100%;
    }*/
}
.messageColumn {
    background-color: #f3e9e7;
    width: 100%;
    padding: 0 10px;
    flex-basis: 80%;
    height: 100%;
    @include flexContainer(column);
    position: relative;
    overflow: hidden;
}
.listName,
.listMessages {
    list-style: none;
    @include flexContainer(column, flex-start, flex-start);
    padding: 0;
}
.listMessages {
    flex-grow: 1;
}
.messagesForm {
    height: 1100px;
    overflow: scroll;
    @media screen and (max-width:2370px)  {
        height: 1000px;
    }
    @media screen and (max-width:2300px)  {
        height: 900px;
    }
    @media screen and (max-width:2100px)  {
        height: 800px;
    }
    @media screen and (max-width:1900px)  {
        height: 700px;
    }
    @media screen and (max-width:1700px)  {
        height: 600px;
    }
    @media screen and (max-width:1500px)  {
        height: 550px;
    }

}
.formArea {
    @include flexContainer(row);
    gap: 5px;
    align-self: center;
    width: 95%;
    margin-bottom: 10px;
}
.textareaMessage {
    width: 98%;
    font-size: 20px;
    padding: 0;
}
.profileName {
    color: black;
    text-decoration: none;
}
.waitMessages {
    background-color: #f3e9e7;
    position: absolute;
    width: 100%;
    height: 100%;
}
.hidden {
    visibility: hidden;
}
.messagesForm::-webkit-scrollbar {
    width: 0;
}
.dialogColumn::-webkit-scrollbar{
    width: 0;
}